@font-face {
  font-family: "robotoBold";
  src: url("../../fonts/Roboto-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
.hero__conteiner--img {
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}

.hero {
  position: relative;
  max-width: 90rem;
  height: 100vh;
  overflow: hidden;
}
.hero__conteiner {
  display: grid;
  justify-items: stretch;
  padding-left: 14.3rem;
  padding-right: 14.3rem;
  gap: 6rem;
  grid-template-columns: 1fr 1fr;
  height: 100%;
}
.hero__conteiner--left {
  padding-top: 6rem;
}
.hero__conteiner--img {
  height: 34.375vh;
  width: 35.069vw;
  background-image: url("../../images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_c_scale,w_784.jpg");
}
.hero__conteiner--right {
  padding-top: 8.5rem;
}
.hero__conteiner--title {
  height: 5vh;
  margin-bottom: 1.5rem;
}
.hero__conteiner--title h1 {
  font-size: 3rem;
  line-height: 3.2rem;
}
.hero__conteiner--paragraph {
  height: 6rem;
  width: 26.389vw;
  margin-bottom: 2rem;
}
.hero__conteiner--paragraph p {
  line-height: 1.5rem;
  font-size: 1rem;
}
.hero__conteiner--btn {
  cursor: pointer;
  padding: 1.5vh 2.778vw;
  border-radius: 32px;
  background-color: #E4C1AE;
  border: none;
  color: #FFFFFF;
}
.hero__conteiner #resetPopupBtn {
  animation-name: openResetBtn;
  animation-duration: 1.5s;
}
@keyframes openResetBtn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@font-face {
  font-family: "robotoBold";
  src: url("../../fonts/Roboto-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
@media (min-width: 230px) and (max-width: 480px) {
  .hero__conteiner {
    padding-left: 0%;
    padding-right: 0%;
    grid-template-columns: 1fr;
    height: 100%;
  }
  .hero__conteiner--left {
    padding-top: 0%;
  }
  .hero__conteiner--img {
    background-image: url("../../../images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_c_scale,w_784.jpg");
    width: 100vw;
    height: 100vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
  .hero__conteiner--right {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    padding-top: 0%;
    height: 100vh;
    width: 100vw;
    text-align: center;
  }
  .hero__conteiner--title {
    width: 100vw;
  }
  .hero__conteiner--title h1 {
    font-size: 13vw;
  }
  .hero__conteiner--paragraph {
    height: auto;
    width: 100vw;
  }
  .hero__conteiner--paragraph p {
    font-size: 7vw;
    line-height: 2rem;
  }
  .hero__conteiner--btn {
    margin: 0.5rem;
    width: 50vw;
    font-size: 8vw;
    background-color: rgb(73, 59, 59);
  }
}

@media (min-width: 480px) and (max-width: 769px) {
  .hero__conteiner {
    padding-left: 0%;
    padding-right: 0%;
    grid-template-columns: 1fr;
    grid-template-rows: 40% 1fr;
    gap: 0;
    height: 100%;
  }
  .hero__conteiner--left, .hero__conteiner--right {
    padding-top: 0%;
  }
  .hero__conteiner--img {
    width: 100vw;
    background-image: url("../../../images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_c_scale,w_784.jpg");
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 95%;
  }
  .hero__conteiner--right {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
  }
  .hero__conteiner--title {
    order: 2;
    height: auto;
    margin-bottom: 0;
  }
  .hero__conteiner--title h1 {
    font-size: 6.5vw;
  }
  .hero__conteiner--paragraph {
    text-align: center;
    width: 98vw;
    height: auto;
    order: 3;
  }
  .hero__conteiner--paragraph p {
    font-size: 2.2vw;
    line-height: 6vh;
  }
  .hero__conteiner--btn {
    margin: 0.5rem;
    font-size: 4vw;
  }
}

@media (min-width: 769px) and (max-width: 960px) {
  .hero__conteiner {
    padding-left: 0%;
    padding-right: 0%;
    grid-template-columns: 1fr;
    grid-template-rows: 40% 1fr;
    gap: 0;
    height: 100%;
  }
  .hero__conteiner--left, .hero__conteiner--right {
    padding-top: 0%;
  }
  .hero__conteiner--img {
    width: 100vw;
    background-image: url("../../../images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_c_scale,w_1234.jpg");
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 95%;
  }
  .hero__conteiner--right {
    display: grid;
    grid-template-areas: "title title title";
    grid-template-rows: calc(33.3333333333% - 25px) calc(25% - 10px);
    justify-content: space-evenly;
    align-items: start;
  }
  .hero__conteiner--title {
    grid-area: title;
    height: auto;
    margin-bottom: 0%;
    text-align: center;
  }
  .hero__conteiner--paragraph {
    width: 50vw;
    grid-area: 2/3;
    margin-bottom: 0%;
  }
  .hero__conteiner--btn {
    margin: 0.5rem;
  }
}

@media (min-width: 960px) and (max-width: 1200px) {
  .hero__conteiner {
    padding-left: 3%;
    padding-right: 0%;
    justify-content: center;
    gap: 2rem;
  }
  .hero__conteiner--img {
    width: 100%;
  }
  .hero__conteiner--right {
    padding-top: 5.8rem;
    text-align: center;
  }
  .hero__conteiner--paragraph {
    width: 95%;
  }
}

.background {
  position: fixed;
  left: 0%;
  top: 0%;
  height: 100vh;
  width: 100vw;
  z-index: 2;
  background-color: #121212;
  opacity: 0.5;
}

.hide {
  display: none;
}

.reset {
  display: none;
}

.bold {
  font-weight: bold;
}

.hero__popup {
  position: fixed;
  width: 57.5rem;
  height: 13.1rem;
  background-color: #FFFFFF;
  z-index: 3;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  animation-name: showPopup;
  animation-duration: 1s;
}
@keyframes showPopup {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.hero__popup--alert {
  font-size: 2.5rem;
  position: absolute;
  left: calc(50% - 190px - 175px);
  top: calc(50% - 25px - 24px);
}
.hero__popup--alertInfo {
  font-size: 1rem;
  position: absolute;
  left: calc(50% - 190px - 175px);
  top: calc(50% - 12px + 37px);
}
.hero__popup--icon {
  position: absolute;
  right: 4%;
  top: 25%;
  cursor: pointer;
}

@media (min-width: 230px) and (max-width: 480px) {
  .hero__popup {
    width: calc(100% - 80px);
  }
  .hero__popup--alert {
    top: 20%;
    left: 50%;
    transform: translate(-50%);
    font-size: 2rem;
  }
  .hero__popup--alertInfo {
    top: 55%;
    left: 10%;
  }
  .hero__popup--icon {
    left: 50%;
    top: 10%;
    font-size: 25px;
  }
}
@media (min-width: 480px) and (max-width: 769px) {
  .hero__popup {
    width: calc(100% - 100px);
  }
  .hero__popup--alert {
    left: 10%;
  }
  .hero__popup--alertInfo {
    left: 10%;
    font-size: 1.5rem;
  }
}
@media (min-width: 769px) and (max-width: 960px) {
  .hero__popup {
    width: calc(100% - 100px);
  }
  .hero__popup--alert {
    left: 10%;
  }
  .hero__popup--alertInfo {
    font-size: 1.5rem;
    left: 10%;
  }
}

.hero_darkModeInfo {
  position: absolute;
  right: 2%;
  top: 2%;
  font-size: 0.7rem;
}

.hero__darkMode {
  z-index: 1;
  position: absolute;
  cursor: pointer;
  right: 3%;
  top: 5%;
  width: 40px;
  height: 20px;
  border-radius: 20px;
  background-color: rgb(205, 208, 211);
  transition: 0.5s;
  box-shadow: inset 0 8px 60px rgba(255, 255, 255, 0.1), inset 0 8px 8px rgba(255, 255, 255, 0.1), inset 0 -4px -4px rgba(255, 255, 255, 0.1);
}
.hero__darkMode--insideIcon {
  position: absolute;
  right: 3%;
  width: 20px;
  height: 20px;
  background: linear-gradient(to bottom, #444, #fff);
  border-radius: 50%;
  transform: scale(0.9);
  box-shadow: 0 8px 40px rgba(255, 255, 255, 0.5), inset 0 4px 4px rgba(0, 0, 0, 0.2), inset 0 -4px 4px rgba(0, 0, 0, 0.2);
  transition: 0.5s;
}

.hero__darkMode.active .hero__darkMode--insideIcon {
  right: 20px;
}

.dark {
  background-color: #421d35;
  background-image: linear-gradient(225deg, #421d35 0%, #573c71 50%, #133a55 100%);
  color: #b3a3ad;
}
.dark .hero__conteiner--btn {
  color: #b3a3ad;
  background-color: #421d35;
}
.dark .hero__conteiner--img {
  opacity: 0.7;
}
@media (min-width: 230px) and (max-width: 480px) {
  .dark .hero__conteiner--img {
    display: none;
  }
}
.dark .hero__popup, .dark .hero__darkMode.active {
  background-color: #6a3056;
  background-image: linear-gradient(225deg, #6a3056 0%, #634e76 50%, #35556b 100%);
}

* {
  font-size: 16px;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "robotoBold", sans-serif;
  font-style: normal;
}